/* eslint-disable */
<template>
  <PlayGround v-if="$store.state.pk.status === 'playing'">

  </PlayGround>

  <MatchGround v-if="$store.state.pk.status === 'matching'">

  </MatchGround>
  <ResultBoard v-if="$store.state.pk.loser !== 'none'">
  </ResultBoard>

  <div class="user-color" v-if="$store.state.pk.status === 'playing'">
    <div class="text"></div>
    <div class="color-red" v-if="parseInt($store.state.user.id) === parseInt($store.state.pk.b_id)"></div>
    <div class="color-blue" v-if="parseInt($store.state.user.id) === parseInt($store.state.pk.a_id)"></div>
  </div>
  <!-- <div class="user-color">
    
    </div> -->
</template>


<script>  
import PlayGround from '../../components/PlayGround.vue'
import MatchGround from '../../components/MatchGround.vue';
import ResultBoard from '../../components/ResultBoard.vue'
import { onMounted, onUnmounted} from 'vue'
import { useStore } from 'vuex'
export default {  
  components: {
    PlayGround,
    MatchGround,
    ResultBoard
  },

  setup() {
    const store = useStore();
    const socketUrl = `wss://app7033.acapp.acwing.com.cn/websocket/${store.state.user.token}/`;
    
    let socket = null;
  
    // 
    onMounted(() => {
      store.commit("updateOpponent", {
        username: "",
        photo: "https://ts1.cn.mm.bing.net/th/id/R-C.c256dbecea080677593c4236b699020a?rik=qK71Iap4gmYNHA&riu=http%3a%2f%2fwww.imanami.com%2fwp-content%2fuploads%2f2016%2f03%2funknown-user.jpg&ehk=K9lZs3cX1UTchIGU%2baD4jf5rQeIyn41WGbDE9Ggxkm4%3d&risl=&pid=ImgRaw&r=0&sres=1&sresct=1",
      })
      socket = new WebSocket(socketUrl);

      socket.onopen = () => {
        console.log("connected");
        store.commit("updateSocket", socket);
      }

      socket.onmessage = msg => {
        const data  = JSON.parse(msg.data);
        
        //  ,
        if (data.event === "start-matching") {
          //  ,
          store.commit("updateOpponent", {
            username: data.opponent_username,
            photo: data.opponent_photo,
          });

          //  
          setTimeout(() => {
          store.commit("updateStatus", "playing");
          },400);
          store.commit("updateGame", data.game);
        } else if (data.event === "move") {
          //  
          console.log(data);
          const game = store.state.pk.gameObject;
          const [snake0, snake1] = game.snakes;
          snake0.set_direction(data.a_direction);
          snake1.set_direction(data.b_direction);


        } else if (data.event === "result") {
          //  
          console.log(data);
          const game = store.state.pk.gameObject;
          const [snake0, snake1] = game.snakes;
            
          if (data.loser === "all" || data.loser === "A") {
            snake0.status = "die";
          }
          if (data.loser === "all" || data.loser === "B"){
            snake1.status = "die";
          }
          //  
          store.commit("updateLoser",data.loser);
        }
      }

      socket.onclose = () => {
        console.log("disconnected");
        store.commit("updateStatus", "matching");
        store.commit("updateLoser","none");
        store.commit("updateIsRecord", false);
      }
    });

    // ,,
    onUnmounted(() => {
      socket.close();
      store.commit("updateStatus", "matching");
      store.commit("updateLoser","none");
      store.commit("updateIsRecord", false);
    })


  }
};  
</script>  
  
<style scoped>  
/*  */  
div.user-color {
   height: 5vh;
   width: 30vh;
   margin-left: 5vh;
   padding: none;

}
div.text {
    font-family: cursive;
    font-size: 1.8em;
    font-weight: 400;
    color: white;
    position: absolute;
    padding: none;
}
div.color-red {
  margin-left: 15vh;
  background-color: #F94848;
  height: 5vh;
  width: 5vh;
  border-radius: 50%;
  position: absolute;
}
div.color-blue {
  margin-left: 15vh;
  background-color: #4876EC;
  height: 5vh;
  width: 5vh;
  border-radius: 50%;
  position: absolute;
}
</style>
