<template>
  <div class="container">
    <div class="row">
      <div class="col-3">
        <div class="card" style="margin-top: 20px;">
          <div class="card-body">
            <img :src="$store.state.user.photo" alt="" style="width: 100%;">
          </div>
        </div>
      </div>
      <div class="col-9">
        <div class="card" style="margin-top: 20px;">
          <div class="card-header">
            <span style="font-size: 120%;">Bot</span>
            <button type="button" class="btn btn-primary float-end" data-bs-toggle="modal" data-bs-target="#add-bot-modal">Bot</button>

            <!-- Modal -->
            <div class="modal fade" id="add-bot-modal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1">
              <div class="modal-dialog modal-dialog modal-dialog-centered modal-xl">
                <div class="modal-content">
                  <div class="modal-header">
                    <h5 class="modal-title" style="font-size: 150%;">Bot</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                  </div>
                  <div class="modal-body">
                    <div class="mb-3">
                      <label for="add-bot-title" class="form-label" style="font-size: 130%;"></label>
                      <input v-model="botadd.title" type="text" class="form-control" id="add-bot-title" placeholder="Bot">
                    </div>
                    <div class="mb-3">
                      <label for="description" class="form-label" style="font-size: 130%;"></label>
                      <textarea v-model="botadd.description" class="form-control" id="description" rows="2" placeholder="Bot"></textarea>
                    </div>
                    <div class="mb-3">
                      <label for="content" class="form-label" style="font-size: 130%;"></label>
                      <VAceEditor
                      v-model:value="botadd.content"
                      @init="editorInit"
                      lang="c_cpp"
                      theme="dawn"
                      style="height: 300px;font-size: large;"
                      :options="{
                      enableBasicAutocompletion: true, //
                      enableSnippets: true, // 
                      enableLiveAutocompletion: true, // 
                      fontSize: 18, //
                      tabSize: 4, // 
                      showPrintMargin: false, //
                      highlightActiveLine: true}"/>
                    </div>
                  </div>
                  <div class="modal-footer">
                    <!--  -->
                    <div class="error-message">{{ botadd.error_message }}</div>
                    <button type="button" class="btn btn-danger" @click="add_bot"></button>
                    <button type="button" class="btn btn-primary" data-bs-dismiss="modal"></button>
                  </div>
                </div>
              </div>
            </div>

          </div>
          <div class="card-body">
            <table class="table table-striped table-hover">
              <thead>
                <tr>
                  <th style="text-align: center;">Bot</th>
                  <th style="text-align: center;"></th>
                  <th style="text-align: center;"></th>
                  <th style="text-align: center;"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="bot in bots" :key="bot.id">
                  <td style="text-align: center;">{{ bot.title }}</td>
                  <td style="text-align: center;">{{ bot.description }}</td>
                  <td style="text-align: center;">{{ bot.createtime }}</td>
                  <td style="text-align: center;">
                    <button type="button" class="btn btn-primary" style="margin-right: 10px;" data-bs-toggle="modal" :data-bs-target="'#update-bot-modal' + bot.id"></button>
                    
                    <!-- Modal -->
                    <div class="modal fade" :id="'update-bot-modal' + bot.id" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1">
                      <div class="modal-dialog modal-dialog modal-dialog-centered modal-xl">
                        <div class="modal-content">
                          <div class="modal-header">
                            <h5 class="modal-title" style="font-size: 150%;">Bot</h5>
                            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                          </div>
                          <div class="modal-body">
                            <div class="mb-3">
                              <label for="update-bot-title" class="form-label" style="font-size: 130%;"></label>
                              <input v-model="bot.title" type="text" class="form-control" id="update-bot-title" placeholder="Bot">
                            </div>
                            <div class="mb-3">
                              <label for="description" class="form-label" style="font-size: 130%;"></label>
                              <textarea v-model="bot.description" class="form-control" id="description" rows="2" placeholder="Bot"></textarea>
                            </div>
                            <div class="mb-3">
                              <label for="content" class="form-label" style="font-size: 130%;"></label>
                              <VAceEditor
                                  v-model:value="bot.content"
                                  @init="editorInit"
                                  lang="c_cpp"
                                  theme="dawn"
                                  style="height: 300px;font-size: large;" 
                                  :options="{
                                  enableBasicAutocompletion: true, //
                                  enableSnippets: true, // 
                                  enableLiveAutocompletion: true, // 
                                  fontSize: 18, //
                                  tabSize: 4, // 
                                  showPrintMargin: false, //
                                  highlightActiveLine: true}"/>
                            </div>
                          </div>
                          <div class="modal-footer">
                            <!--  -->
                            <div class="error-message">{{ botupdate.error_message }}</div>
                            <button type="button" class="btn btn-danger"  @click="update_bot(bot)"></button>
                            <button type="button" class="btn btn-primary" data-bs-dismiss="modal"></button>
                          </div>
                        </div>
                      </div>
                    </div>
                    
                    <button type="button" class="btn btn-danger" @click="remove_bot(bot)"></button>
                  </td>
                </tr>
              </tbody>
            </table>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>  
import { ref, reactive } from 'vue'
import $ from 'jquery'
import { useStore } from 'vuex'
import { Modal } from 'bootstrap/dist/js/bootstrap'
import { VAceEditor } from 'vue3-ace-editor';
import ace from 'ace-builds';
import 'ace-builds/src-noconflict/mode-c_cpp';
import 'ace-builds/src-noconflict/mode-json';
import 'ace-builds/src-noconflict/theme-dawn';
import 'ace-builds/src-noconflict/ext-language_tools';

export default {  
  components: {
    VAceEditor,
  },

  setup() {
    // 
    ace.config.set(
    "basePath", 
    "https://cdn.jsdelivr.net/npm/ace-builds@" + require('ace-builds').version + "/src-noconflict/")

    const store = useStore();
    let bots = ref([]);

    const botadd = reactive({
      title: "",
      description: "",
      content: "",
      error_message: "",
    })

    const botupdate = reactive({
      title: "",
      description: "",
      content: "",
      error_message: "",
    })

    const refresh_bots = () => {
      $.ajax({
        url: "https://app7033.acapp.acwing.com.cn/api/user/bot/getlist/",
        type: "GET",
        headers: {
          Authorization: "Bearer " + store.state.user.token,
        },
        success(resp) {
          bots.value = resp;
        }
      })
    }

    refresh_bots();

    const add_bot = () => {
      botadd.error_message = "";
      $.ajax({
        url: "https://app7033.acapp.acwing.com.cn/api/user/bot/add/",
        type: "POST",
        data: {
          // reactive.value,ref.value
          title: botadd.title,
          description: botadd.description,
          content: botadd.content,
        },
        headers: {
          Authorization: "Bearer " + store.state.user.token,
        },
        success(resp) {
          if (resp.error_message === "success") {
            // 
            botadd.title = "";
            botadd.description = "";
            botadd.content = "";
            Modal.getInstance("#add-bot-modal").hide();

            refresh_bots();
          } else {
            botadd.error_message = resp.error_message;
          }
        }

      })
    }

    const remove_bot = (bot) => {

      $.ajax({
        url: "https://app7033.acapp.acwing.com.cn/api/user/bot/remove/",
        type: "POST",
        data: {
          // reactive.value,ref.value
          bot_id: bot.id,
          
        },
        headers: {
          Authorization: "Bearer " + store.state.user.token,
        },
        success(resp) {
          if(resp.error_message === "success")
          {
            refresh_bots();
          }
        }
      })
    }

    const update_bot = (bot) => {
      botupdate.error_message = "";
      $.ajax({
        url: "https://app7033.acapp.acwing.com.cn/api/user/bot/update/",
        type: "POST",
        data: {
          // reactive.value,ref.value
          bot_id: bot.id,
          title: bot.title,
          description: bot.description,
          content: bot.content,
        },
        headers: {
          Authorization: "Bearer " + store.state.user.token,
        },
        success(resp) {
          if (resp.error_message === "update success") {
            Modal.getInstance("#update-bot-modal" + bot.id).hide();
            refresh_bots();
          } else {
            botupdate.error_message = resp.error_message;
          }
        }
      })
    }

    return {
      bots,
      botadd,
      botupdate,
      add_bot,
      remove_bot,
      update_bot,
    }
  }

};  
</script>  
  
<style scoped>  
/*  */  
div.error-message {
  color: red;
}
</style>
