<template>
    <HomeField>
      <div class="row justify-content-md-center">
        <div class="col-3 gradient-border" id="box">
          <form @submit.prevent="register">
            <div class="mb-3">
              <label for="username" class="form-label" style="color: black;"></label>
              <input v-model="username" type="text" class="form-control" id="username" placeholder="">
            </div>
            <div class="mb-3">
              <label for="password" class="form-label" style="color: black;"></label>
              <input v-model="password" type="password" class="form-control" id="password" placeholder="">
            </div>
            <div class="mb-3">
              <label for="confirmPassword" class="form-label" style="color: black;"></label>
              <input v-model="confirmPassword" type="password" class="form-control" id="confirmPassword" placeholder="">
            </div>
            <div class="error-message">{{ error_message }}</div>
            <button type="submit" class="btn btn-primary"  style="background-image: linear-gradient(rgba(241, 157, 172, 1),rgba(255, 255, 170, 1));"></button>
          </form>

        </div>
      </div>
    </HomeField>
  </template>
  
  
  <script>  
  import HomeField from '../../../components/HomeField.vue'
  import { ref } from 'vue'
  import router from '../../../router/index'
  import $ from 'jquery'

  export default {  
    components: {
      HomeField,
    },
    setup() {
      let username = ref('');
      let password = ref('');
      let confirmPassword = ref('');
      let error_message = ref('');

      const register = () => {
        $.ajax({
          // 
          url: "https://app7033.acapp.acwing.com.cn/api/user/account/register/",
          type: "post",
          data: {
            username: username.value,
            password: password.value,
            confirmPassword: confirmPassword.value,
          },
          success(resp) {
            if (resp.error_message === "success" ) {
              alert("...");
            setTimeout(() => {
              router.push({name: "user_account_login"});
            }, 500); // 0.5
            } else {
              error_message.value = resp.error_message;
            }
          }
        })
      }

      return {
        username,
        password,
        confirmPassword,
        error_message,
        register
      }
    }
  };  
  </script>  
    
  <style scoped>  
  /*  */  
  button {
    /*  */
    width: 100%;
  }
  div.error-message {
    color: red;
  }

  
  @import url('https://fonts.googleapis.com/css?family=Raleway:200');

#box {
  padding: 30px 45px 30px 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  height: auto;
  color: white;
}
.gradient-border {
  --borderWidth: 3px;
  background: white;
  position: relative;
  border-radius: 15px;
}
.gradient-border:after {
  content: '';
  position: absolute;
  top: calc(-1 * var(--borderWidth));
  left: calc(-1 * var(--borderWidth));
  height: calc(100% + var(--borderWidth) * 2);
  width: calc(100% + var(--borderWidth) * 2);
  background: linear-gradient(60deg, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82);
  border-radius: calc(15px);
  z-index: -1;
  animation: animatedgradient 3s ease alternate infinite;
  background-size: 300% 300%;
}


@keyframes animatedgradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


  </style>
  