<template>
   <div class="container content-field">
      <div class="card1">
        <div class="card-body">
          <slot>
            <div class="card interact">
              <h3>Bot</h3>
              BotBot<br><br>
              
              <h5></h5>
              <ul>
                <li>#Sx#Sy#()#Sx#Sy#()</li>
                <li>Bot~</li>
              </ul>
              <h5></h5>
              <ul>
                <li>0   |  1   |  2   |  3 </li>
                <li>'nextMove'</li>
              </ul>
              <h5></h5>
              <ul>
                11111111111111100011010000011001000000000110001100001001100000001000011001000000000110000100100001100000000010011000010000000110010000110001100000000010011000001011000111111111111111#11#1#(011101210010)#1#12#(233322233033)
              </ul>
              <h5></h5>
              <ul>
                <li>012101</li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
            <div class="card code">
              <h3 style="margin-bottom: 15px;">Bot</h3>
                <VAceEditor
                  lang="c_cpp"
                  theme="dracula"
                  style="height: 1850px; width: 100%; font-size: large;"
                  :options="{
                    enableBasicAutocompletion: true,
                    enableSnippets: true,
                    enableLiveAutocompletion: true,
                    fontSize: 18,
                    tabSize: 4,
                    showPrintMargin: false,
                    highlightActiveLine: true,
                    readOnly: true,
                  }"
                  v-model:value="code"
                />
            </div>
          </slot>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { VAceEditor } from 'vue3-ace-editor';
  import { ref } from 'vue';  
  import ace from 'ace-builds';
  import 'ace-builds/src-noconflict/theme-dracula';
  import 'ace-builds/src-noconflict/mode-c_cpp';
  import 'ace-builds/src-noconflict/ext-language_tools';
  
  export default {
    components: {
      VAceEditor
    },
    
    setup() {
        const code = ref(`package com.kob.botrunningsystem.utils;

import java.io.File;
import java.io.FileNotFoundException;
import java.util.ArrayList;
import java.util.List;
import java.util.Scanner;

public class Bot implements java.util.function.Supplier<Integer> {
    static class Cell {
        public int x, y;
        public Cell(int x, int y) {
            this.x = x;
            this.y = y;
        }
    }

    private boolean check_tail_increasing(int step) {  // 
        if (step <= 10) return true;
        return step % 3 == 1;
    }

    //  
    public List<Cell> getCells(int sx, int sy, String steps) {
        steps = steps.substring(1, steps.length() - 1);
        List<Cell> res = new ArrayList<>();

        int[] dx = {-1, 0, 1, 0}, dy = {0, 1, 0, -1};
        int x = sx, y = sy;
        int step = 0;
        res.add(new Cell(x, y));
        for (int i = 0; i < steps.length(); i ++ ) {
            int d = steps.charAt(i) - '0';
            x += dx[d];
            y += dy[d];
            res.add(new Cell(x, y));
            if (!check_tail_increasing( ++ step)) {
                res.remove(0);
            }
        }
        return res;
    }


    //  ,Bot
    //  0   |  1   |  2   |  3 
    public Integer nextMove(String input) {
        String[] strs = input.split("#");
        int[][] g = new int[13][14];

        //  
        for (int i = 0, k = 0; i < 13; i ++ ) {
            for (int j = 0; j < 14; j ++, k ++ ) {
                if (strs[0].charAt(k) == '1') {
                    g[i][j] = 1;
                }
            }
        }

        // 
        int aSx = Integer.parseInt(strs[1]), aSy = Integer.parseInt(strs[2]);
        int bSx = Integer.parseInt(strs[4]), bSy = Integer.parseInt(strs[5]);

        List<Cell> aCells = getCells(aSx, aSy, strs[3]);
        List<Cell> bCells = getCells(bSx, bSy, strs[6]);

        //  
        for (Cell c: aCells) g[c.x][c.y] = 1;
        for (Cell c: bCells) g[c.x][c.y] = 1;

/*---------------------------------------------------------------------------*/

        // 
        int[] dx = {-1, 0, 1, 0}, dy = {0, 1, 0, -1};
        for (int i = 0; i < 4; i ++ ) {
            int x = aCells.get(aCells.size() - 1).x + dx[i];
            int y = aCells.get(aCells.size() - 1).y + dy[i];
            if (x >= 0 && x < 13 && y >= 0 && y < 14 && g[x][y] == 0) {
                return i;
            }
        }

        return 0;
/*-----------------------------------------------------------------------------------------*/
    }
    
    @Override
    public Integer get() {
        File file = new File("input.txt");
        try {
            Scanner sc = new Scanner(file);
            return nextMove(sc.next());
        } catch (FileNotFoundException e) {
            throw new RuntimeException(e);
        }
    }
}`);
      // 
      ace.config.set(
        "basePath",
        "https://cdn.jsdelivr.net/npm/ace-builds@" + require('ace-builds').version + "/src-noconflict/"
      );

      return {
        code
      };
    }
  };
  </script>
  
